import type { ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { RadarChartOpinionOptions } from '../types/radar-chart-opinion-options';
import type { StatementGraph } from '../types/statement-graph';

interface InternalOptions {
  options: RadarChartOpinionOptions;
  statements: (Statement & StatementGraph)[];
}

/**
 * The RadarChartOpinionInternal class contains helper methods for the
 * RadarChartOpinion class.
 */
export default class RadarChartOpinionInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {RadarChartOpinionOptions} param.options - Additional options for the
   *                                                   bar graph.
   * @param {(Statement & StatementGraph)[]} param.statements - The statements for the graph.
   * @returns The chart data, and the goal dataset
   */
  public static getChartData({ options, statements }: Readonly<InternalOptions>): {
    datasets: ChartDataset<'radar', number[]>[];
  } {
    return {
      datasets: this.getRadarData({ options, statements }),
    };
  }

  /**
   * Get the dataset for the radar graph
   *
   * @param {Object} param - The parameter object.
   * @param {RadarChartOpinionOptions} param.options - Additional options for the
   *                                                   bar graph.
   * @param {(Statement & StatementGraph)[]} param.statements - The statements for the graph.
   * @returns The dataset for the graph
   */
  public static getRadarData({ options, statements }: Readonly<InternalOptions>):
  ChartDataset<'radar', number[]>[] {
    console.log('kaas graph', statements[0].graph);

    const classCount = statements[0]?.graph.classvalues.length || 0;
    const meanAll = Array.from(
      { length: classCount },
      (_, i) => statements.reduce(
        (sum, statement) => sum + (statement.graph.classvalues[i] || 0),
        0,
      ) / statements.length,
    );

    // const meanAll: number[] = [];
    // statements.forEach((statement) => {
    //   statement.graph.classvalues.forEach((value, i) => {
    //     if (meanAll[i] === undefined) {
    //       meanAll[i] = value;
    //     } else {
    //       meanAll[i] += value;
    //     }
    //   });
    // });

    console.log('kaas meanAll', meanAll);

    return [{
      data: [],
    }];
  }
}
