import {
  ArcElement, Chart, Legend, PieController, Tooltip,
} from 'chart.js';

import PieChartReceivedMarksInternal from './pie-chart-received-marks.internal';
import PieChartReceivedMarksOptionsObject from '../types/pie-chart-received-marks-options';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { PieChartReceivedMarksOptions } from '../types/pie-chart-received-marks-options';

/**
 * The PieChartReceivedMarks is a chart type that displays the received marks
 * as a pie chart.
 */
export default class PieChartReceivedMarks implements IGraph {
  /** The options for the chart. */
  #options: PieChartReceivedMarksOptions;

  /** The statements to be displayed in the chart. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the chart.
   * @param {PieChartReceivedMarksOptions} param.options - The options for the chart.
   */
  constructor(
    {
      statements, options,
    }:
    Readonly<{
      statements: Statement[];
      options: PieChartReceivedMarksOptions;
    }>,
  ) {
    this.#statements = statements;
    // Parse and set the options using the provided options or default values
    this.#options = PieChartReceivedMarksOptionsObject.parse(options);

    // Register the necessary components for the Chart.js library
    Chart.register(
      PieController,
      ArcElement,
      Legend,
      Tooltip,
    );
  }

  public draw(): void {
    try {
      // eslint-disable-next-line no-new
      new Chart(this.#options.ctx, {
        type: 'pie',
        data: PieChartReceivedMarksInternal.getChartData({
          options: this.#options,
          statements: this.#statements,
        }),
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: true,
              position: 'top',
            },
            tooltip: {
              enabled: true,
            },
          },
        },
      });
    } catch (error) {
      throw Error(`Error drawing pie chart: ${error}`);
    }
  }
}
