import { BAR_COLORS } from '../constants/colors';
import NotDefined from '../types/errors/not-defined';

/**
 * Parameters for retrieving a background color.
 * - `index`: The index for selecting a color from the color scale array.
 * - `key` (optional): The key for selecting a named background color, if named colors are used.
 */
interface BackgroundColorParams {
  index: number;
  key?: string;
}

/**
 * Interface used to constrain the generic type T in ColorScale.
 * It includes optional properties for the color scale string and
 * an array of background colors.
 * - `graphColorScale`: The color scale string.
 * - `backgroundColors`: An array of background colors.
 */
interface ColorScaleParams {
  graphColorScale?: string;
  backgroundColors?: string[];
}

/**
 * ColorScale is a helper class for managing color scale strings.
 * It encapsulates a color scale string and provides a foundation for
 * color-related operations in graph visualizations.
 */
export default class ColorScale<T extends ColorScaleParams> {
  /**
   * The array of background colors derived from the color scale string.
   */
  #backgroundColors: string[] = [...BAR_COLORS];

  /**
   * The color scale string, which defines the color mapping.
   */
  #colorString: string | undefined;

  /**
   * Indicates whether named background colors are used. The default is false.
   */
  #hasNamedBackgroundColors = false;

  /**
   * The map of named background colors.
   */
  #namedBackgroundColors: Map<string, string> = new Map<string, string>();

  /**
   * Wether to use named background colors.
   */
  public get hasNamedBackgroundColors(): boolean {
    return this.#hasNamedBackgroundColors;
  }

  /**
   * Constructs a new ColorScale instance.
   *
   * @param {Object} param - The parameter object.
   * @param {string} param.graphColorScale - The color scale string to use for color mapping.
   * @param {string[]} param.backgroundColors - An array with background colors.
   */
  constructor({ graphColorScale, backgroundColors }: T) {
    this.#colorString = graphColorScale;
    if (backgroundColors !== undefined) {
      this.#backgroundColors = backgroundColors;
    }

    // Validate that the color scale string is not empty
    if (this.#colorString !== undefined && this.#colorString !== '') {
      // Split the color scale string by commas to get individual color definitions
      const splittedColorScale = this.#colorString.split(',');

      // If there exist colons in the color scale, it indicates named colors
      if (this.#colorString.includes(':')) {
        this.#hasNamedBackgroundColors = true;

        splittedColorScale.forEach((color) => {
          // Split each color definition by colon to separate name and value
          const [key, value] = color.split(':');
          // Store the named color in the map
          this.#namedBackgroundColors.set(key, value);
        });
      } else {
        // Otherwise, use the split colors as background colors
        this.#backgroundColors = splittedColorScale;
      }
    }
  }

  /**
   * Retrieves the background color based on the provided key or index.
   * @param {Object} param - The parameter object containing either a key or an index.
   * @param {number} param.index - The index for background colors (required if not
   *                             using named colors).
   * @param {string | undefined} param.key - The key for named background colors (required if
   *                                       using named colors).
   *
   * @returns The corresponding background color as a string.
   * @throws {NotDefined} Will throw if `key` is undefined and named background colors are used.
   */
  public backgroundColor({ index, key }: BackgroundColorParams): string {
    if (!this.#hasNamedBackgroundColors) {
      // Return the background color based on the index, cycling through the available colors
      return this.#backgroundColors[index % this.#backgroundColors.length];
    }

    // When using named background colors, a key is required
    if (key === undefined) {
      throw new NotDefined('Key is required when using named background colors.');
    }

    // Retrieve the background color from the map using the provided key
    // If the key does not exist, fallback to using the index-based color
    return this.#namedBackgroundColors.get(key)
      ?? this.#backgroundColors[index % this.#backgroundColors.length];
  }
}
