import {
  BarController, BarElement, CategoryScale, Chart, Legend, LinearScale,
  Tooltip,
} from 'chart.js';
import annotationPlugin from 'chartjs-plugin-annotation';

import BarGraphCombinedMarksInternal
  from './bar-graph-combined-marks.internal';
import HorizontalBarGraphCombinedMarksOptionsObject
  from '../types/horizontal-bar-graph-combined-marks-options';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { HorizontalBarGraphCombinedMarksOptions }
  from '../types/horizontal-bar-graph-combined-marks-options';

/**
 * The HorizontalBarGraphCombinedMarks is a graph type that displays the scores
 * for groups in a horizontal bar chart format with a passing grade line.
 */
export default class HorizontalBarGraphCombinedMarks implements IGraph {
  /** The options for the chart. */
  #options: HorizontalBarGraphCombinedMarksOptions;

  /** The statements to be displayed in the chart. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the chart.
   * @param {HorizontalBarGraphCombinedMarksOptions} param.options - The options for the chart.
   */
  constructor(
    {
      statements, options,
    }:
    Readonly<{
      statements: Statement[];
      options: HorizontalBarGraphCombinedMarksOptions;
    }>,
  ) {
    this.#statements = statements;
    // Parse and set the options using the provided options or default values
    this.#options = HorizontalBarGraphCombinedMarksOptionsObject.parse(options);

    // Register the necessary components for the Chart.js library
    Chart.register(
      BarController,
      BarElement,
      CategoryScale,
      Legend,
      LinearScale,
      Tooltip,
      annotationPlugin,
    );
  }

  public draw(): void {
    try {
      const {
        labels,
        datasets,
        goalAnnotations,
      } = BarGraphCombinedMarksInternal.getChartData({
        horizontal: true,
        options: this.#options,
        statements: this.#statements,
      });

      // eslint-disable-next-line no-new
      new Chart(this.#options.ctx, {
        type: 'bar',
        data: {
          labels,
          datasets,
        },
        options: {
          transitions: {
            show: {
              animations: {
                x: {
                  from: 0,
                },
                y: {
                  from: 0,
                },
              },
            },
          },
          indexAxis: 'y', // This makes the bar chart horizontal
          responsive: true,
          maintainAspectRatio: false,
          events: ['click'],
          scales: {
            x: {
              stacked: false,
              beginAtZero: true,
              position: 'bottom',
              min: 0,
              max: 100,
            },
            y: {
              stacked: true,
              ticks: {
                autoSkip: false,
              },
            },
          },
          plugins: {
            legend: {
              display: true,
              position: 'bottom',
              labels: {
                generateLabels: () => [{
                  text: 'Passing Score',
                  fillStyle: this.#options.goalColor,
                  strokeStyle: this.#options.goalColor,
                  lineWidth: 3,
                  hidden: false,
                  index: 0,
                }],
              },
            },
            tooltip: {
              enabled: true,
            },
            annotation: {
              annotations: goalAnnotations,
            },
          },
        },
      });
    } catch (error) {
      throw Error(`Error drawing horizontal-bar-graph-combined-marks: ${error}`);
    }
  }
}
