import z from 'zod';

import GraphOptionsObject from './graph-options';
import { BAR_COLORS, GOAL_COLOR } from '../constants/colors';

const HorizontalBarGraphCombinedMarksOptionsObject = z.intersection(
  GraphOptionsObject,
  z.object({
    backgroundColors: z.string().array().optional().default(BAR_COLORS),
    goalColor: z.string().optional().default(GOAL_COLOR),
    graphColorScale: z.string().optional(),
    trackingPassed: z.union([z.string(), z.null()]).optional().default(null),
  }),
);

export default HorizontalBarGraphCombinedMarksOptionsObject;

/**
 * The options for the polar chart subject overview.
 *
 * @property {string[]} backgroundColors - The background colors of the pie chart. Default: [
 *                                       '#FF6384', '#4BC0C0', '#FFCD56', '#C9CBCF', '#36A2EB',
 *                                       '#B284BE'
 *                                       ]
 * @property {string} goalColor - The color for the goal line. Default: '#775DD0'
 * @property {string} graphColorScale - The color scale string for the graph.
 * @property {string | null} trackingPassed - The tracking status that indicates a passed goal.
 */
export type HorizontalBarGraphCombinedMarksOptions = z
  .infer<typeof HorizontalBarGraphCombinedMarksOptionsObject>;
