import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { PieChartReceivedMarksOptions } from '../types/pie-chart-received-marks-options';

interface InternalOptions {
  options: PieChartReceivedMarksOptions;
  statements: Statement[];
}

/**
 * The PieChartReceivedMarksInternal class contains helper methods for the
 * PieChartReceivedMarks class.
 */
export default class PieChartReceivedMarksInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {LineGraphOwnMarksOptions} param.options - Additional options for the chart.
   * @param {Statement[]} param.statements - The statements for the chart.
   * @returns The chart dataset
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData {
    const { datasets } = this.getDatasets({ statements, options });

    return {
      labels: [options.correctLabel, options.incorrectLabel],
      datasets,
    };
  }

  /**
   * Get the dataset for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {PieChartReceivedMarksOptions} param.options - Additional options for the chart.
   * @param {Statement[]} param.statements - The statements for the chart.
   * @returns The datasets for the graph
   */
  public static getDatasets({ options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset<'pie', number[]>[];
    } {
    const { numberOfCorrect, numberOfIncorrect } = this.getData({ options, statements });
    const dataset: ChartDataset<'pie', number[]> = {
      type: 'pie',
      backgroundColor: options.backgroundColors,
      borderColor: options.borderColors,
      borderWidth: 1,
      data: [numberOfCorrect, numberOfIncorrect],
    };

    return { datasets: [dataset] };
  }

  /**
   * Get the data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {PieChartReceivedMarksOptions} param.options - Additional options for the chart.
   * @param {Statement[]} param.statements - The statements for the chart.
   * @returns The data for the chart
   */
  public static getData({ options, statements }:
  Readonly<InternalOptions>): {
      numberOfCorrect: number;
      numberOfIncorrect: number;
    } {
    // If there are no statements, we return 0 for both correct and incorrect.
    if (statements.length === 0) {
      return { numberOfCorrect: 0, numberOfIncorrect: 0 };
    }

    // If dashboard mode is enabled, we take count the number of correct and incorrect
    // for all statements. If not, we only take the first statement. Also, we filter
    // the statements to only include those that have the
    // 'http://xerte.org.uk/result/grouping/scores' extension defined.
    let usedStatements: Statement[] = [];
    if (options.dashboardMode) {
      usedStatements = statements.filter(
        (statement) => statement.result?.extensions?.[
          'http://xerte.org.uk/result/grouping/scores'
        ] !== undefined,
      );
    } else {
      usedStatements = statements.slice(0, 1).filter(
        (statement) => statement.result?.extensions?.[
          'http://xerte.org.uk/result/grouping/scores'
        ] !== undefined,
      );
    }

    // Count the number of correct and incorrect answers based on the scores
    // in the 'http://xerte.org.uk/result/grouping/scores' extension for all
    // statements in the usedStatements.
    const { numberOfCorrect, numberOfIncorrect } = usedStatements.reduce(
      (fullAcc, statement) => {
        const questionScores = statement.result?.extensions?.[
          'http://xerte.org.uk/result/grouping/scores'
        ];

        if (questionScores !== undefined) {
          // Go over all scores from the 'http://xerte.org.uk/result/grouping/scores'
          // extension and count the number of correct and incorrect answers.
          // A score of 100 is considered correct, everything else is incorrect.
          const { correct, incorrect }:
          { correct: number; incorrect: number } = questionScores.reduce(
            (acc, [_, score]) => {
              if (score === 100) {
                // If the score is 100, we consider it correct.
                return {
                  correct: acc.correct + 1, incorrect: acc.incorrect,
                };
              }
              // If the score is not 100, we consider it incorrect.
              return {
                correct: acc.correct, incorrect: acc.incorrect + 1,
              };
            },
            { correct: 0, incorrect: 0 },
          );

          return {
            numberOfCorrect: fullAcc.numberOfCorrect + correct,
            numberOfIncorrect: fullAcc.numberOfIncorrect + incorrect,
          };
        }
        return fullAcc;
      },
      { numberOfCorrect: 0, numberOfIncorrect: 0 },
    );

    return { numberOfCorrect, numberOfIncorrect };
  }
}
