export default class ScoreIndexHelper {
  /**
   * Calculates the index for a given score based on the chart's configuration.
   *
   * This method determines the appropriate index for a score, either by using
   * predefined block ranges or by dividing the score into evenly distributed bars.
   *
   * @param {Object} props - The parameter object containing the necessary inputs.
   * @param {number} props.score - The score for which the index needs to be calculated.
   * @param {number} [props.nrOfBars] - The total number of bars in the chart. This is used
   *                                    when block ranges are not provided.
   * @param {string[]} [props.blocks] - An array of string ranges defining the blocks. Each
   *                                    string range should be in the format "start,end".
   *                                    For example: ["0,20", "20,40", "40,60", "60,80", "80,100"].
   * @returns {number | undefined} - The calculated index for the score. Returns `undefined`
   *                                 if the score does not fall within any defined block range.
   *                                 Throws an error if neither `nrOfBars` nor `blocks` are
   *                                 provided.
   *
   * @throws {Error} - Throws an error if both `nrOfBars` and `blocks` are undefined, as at least
   *                   one of these parameters is required to calculate the index.
   *
   * @example
   * // Example using block ranges:
   * const index = ScoreIndexHelper.getIndex({
   *   score: 45,
   *   blocks: ["0,20", "20,40", "40,60", "60,80", "80,100"]
   * });
   * console.log(index); // Output: 2
   *
   * @example
   * // Example using number of bars:
   * const index = ScoreIndexHelper.getIndex({
   *   score: 45,
   *   nrOfBars: 5
   * });
   * console.log(index); // Output: 2
   */
  public static getIndex({ score, nrOfBars, blocks }:
  Readonly<{ score: number; nrOfBars?: number; blocks?: string[] }>): number | undefined {
    if (blocks !== undefined) {
      const ranges = blocks.map(
        (stringRange) => stringRange.split(',').map(
          (stringValue) => Number(stringValue),
        ),
      );

      const index = ranges.findIndex(
        (range) => score >= range[0]
        && (score < range[1] || (range[1] === 100 && score === range[1])),
      );

      return index === -1 ? undefined : index;
    }

    if (!nrOfBars) {
      throw new Error('nrOfBars and blockRanges are not defined');
    }

    const index = Math.floor(
      (score / Math.ceil(100 / nrOfBars)),
    );

    return index >= nrOfBars ? nrOfBars - 1 : index;
  }
}
