import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { LineGraphOwnMarksOptions } from '../types/line-graph-own-marks-options';

interface InternalOptions {
  options: LineGraphOwnMarksOptions;
  statements: Statement[];
}

/**
 * The LineGraphOwnMarksInternal class contains helper methods for the
 * LineGraphOwnMarks class.
 */
export default class LineGraphOwnMarksInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {LineGraphOwnMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The chart data
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData {
    const { datasets } = this.getDatasets({ statements, options });

    return {
      datasets,
    };
  }

  /**
   * Get the dataset for the graph
   *
   * @param {Object} param - The parameter object.
   * @param {LineGraphOwnMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The datasets for the graph
   */
  public static getDatasets({ options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset<'line', { x: number; y: number }[]>[];
    } {
    const { data } = this.getUserData({ statements });

    const dataset: ChartDataset<'line', { x: number; y: number }[]> = {
      type: 'line',
      label: 'data',
      backgroundColor: options.barColors,
      borderColor: options.barColors,
      data,
    };

    return { datasets: [dataset] };
  }

  /**
   * Get the average data
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The user's own data
   */
  public static getUserData({ statements }: Readonly<{ statements: Statement[] }>):
  { data: { x: number; y: number }[] } {
    const data = statements.map((statement) => (
      {
        x: new Date(statement.timestamp ?? Date.now()).getTime(),
        y: (statement.result?.score?.raw ?? 0) / 10,
      }
    ));

    return { data };
  }
}
