import NumberOfBarsHelper from '../helpers/number-of-bars';
import RangeLabelsHelper from '../helpers/range-labels';
import ScoreIndexHelper from '../helpers/score-index';

import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { BarGraphReceivedMarksOptions } from '../types/bar-graph-received-marks-options';

interface InternalOptions {
  options: BarGraphReceivedMarksOptions;
  statements: Statement[];
}

/**
 * The BarGraphReceivedMarksInternal class contains helper methods for the
 * BarGraphReceivedMarks class.
 */
export default class BarGraphReceivedMarksInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphReceivedMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The chart data
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData {
    const {
      datasets, groupAverage, participantsPerGroup, ownScoreLabel,
    } = this.getDatasets({ options, statements });
    const labels = this.getLabels({
      groupAverage, participantsPerGroup, ownScoreLabel, options,
    });

    return {
      datasets,
      labels,
    };
  }

  /**
   * Get the labels for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {[number, number][]} param.groupAverage - The average score for each group
   * @param {number[]} param.participantsPerGroup - The number of participants in each group
   * @param {[number, number]} param.ownScoreLabel - The label for the own score and where
   * to place it
   * @returns The labels for the chart
   */
  public static getLabels(
    {
      groupAverage, participantsPerGroup, ownScoreLabel, options,
    }:
    Readonly<{
      groupAverage?: [number, number][];
      participantsPerGroup?: number[];
      ownScoreLabel?: [number, number];
      options: BarGraphReceivedMarksOptions;
    }>,
  ): string[][] {
    if (options.nrOfBars === undefined) {
      throw new Error('nrOfBars is not defined');
    }

    const labels = RangeLabelsHelper.getRangeLabels({
      followBlock: options.followBlock,
      blockRanges: options.blockRanges,
      nrOfBars: options.nrOfBars,
      blockNames: options.blockNames,
    });

    // Add the average score to each label for the bars
    if (options.showAvg) {
      if (groupAverage === undefined) {
        throw new Error('groupAverage is not defined');
      }
      labels.map(
        (label, i) => label.push(`${options.groupScoreLabel}: ${groupAverage[i][1].toFixed(2)}`),
      );
    }

    // Show the score for the current user
    if (options.showScore) {
      if (ownScoreLabel !== undefined) {
        const [index, labelScore] = ownScoreLabel;
        labels[index].push(`${options.userScoreLabel}: ${labelScore}`);
      }
    }

    // Add the number of participants to each label for the bars
    if (options.showNumberOfParticipants) {
      labels.map(
        (label, i) => label.push(`${participantsPerGroup?.[i] ?? 0} ${options.groupUsersLabel}`),
      );
    }

    return labels;
  }

  /**
   * Get the datasets for the graph
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphReceivedMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The datasets for the graph, the group average data, the
   * users own score, and the number of participants per group
   */
  public static getDatasets({ options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset[];
      groupAverage?: [number, number][];
      ownScore?: [number, number][];
      ownScoreLabel?: [number, number];
      participantsPerGroup?: number[];
    } {
    const datasets: ChartDataset[] = [];
    const backgroundColor = [...options.barColors ?? []];

    let groupAverage;

    // Get the data for the general graph that shows the received marks
    const { data, participantsPerGroup } = this.getData({ options, statements });

    // Get the data for the own score part of the graph, and the border color for the
    // bar that the current user's score falls into
    const {
      ownScore,
      ownScoreLabel,
      borderColor,
    } = this.getOwnScore({ options, statements, data });

    if (options.showAvg) {
      groupAverage = this.getGroupAverage({ options, statements, data });

      if (options.showAvgLine) {
        // Add the dataset for the (black) line that shows the user's own score
        datasets.push({
          type: 'bar',
          label: options.ownScoreLabel,
          data: ownScore,
          backgroundColor: '#000000',
        });

        // Add the dataset for the (brown) lines that shows the average score
        datasets.push({
          type: 'bar',
          label: options.averageScoreBarLabel,
          data: groupAverage,
          backgroundColor: '#8f4e4e',
        });
      }
    }

    // Add the dataset for the general graph that shows the received marks
    datasets.push({
      type: 'bar',
      data,
      backgroundColor,
      borderWidth: 5,
      borderColor,
    });

    return {
      datasets, groupAverage, ownScore, ownScoreLabel, participantsPerGroup,
    };
  }

  /**
   * Get the data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphReceivedMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The data for the chart, and the participantsPerGroup counts per score range
   */
  public static getData({ options, statements }:
  Readonly<InternalOptions>): { data: number[]; participantsPerGroup: number[] } {
    if (!options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }
    if (options.followBlock === undefined) {
      throw new Error('followBlock is not defined');
    }

    // Initialize the data array with zeros
    const data = Array.from({
      length: NumberOfBarsHelper.getNumberOfBars({
        followBlock: options.followBlock,
        blockRanges: options.blockRanges,
        nrOfBars: options.nrOfBars,
      }),
    }, () => 0);

    // Count the number of statements for each score range
    statements.forEach((statement) => {
      const index = ScoreIndexHelper.getIndex({
        score: statement.result?.score?.raw ?? 0,
        nrOfBars: options.nrOfBars,
        blocks: options.followBlock ? options.blockRanges : undefined,
      });
      if (index !== undefined) {
        data[index] += 1;
      }
    });

    const participantsPerGroup = [...data];

    // Convert to percentage if the statements array is not empty
    if (statements.length !== 0) {
      return {
        data: data.map((dataPoint) => (dataPoint / statements.length) * 100),
        participantsPerGroup,
      };
    }

    return { data, participantsPerGroup };
  }

  /**
   * Get the average data of the group
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphReceivedMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @param {number[]} param.data - The data for the general graph. This needs to be used when
   *                                graphFitInfoInBar is set.
   * @returns The average data of the group
   */
  public static getGroupAverage({ options, statements, data }:
  Readonly<InternalOptions & { data: number[] }>): [number, number][] {
    if (!options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }
    if (options.followBlock === undefined) {
      throw new Error('followBlock is not defined');
    }

    // Initialize the data array with zeros
    const groupData = Array.from(
      {
        length: NumberOfBarsHelper.getNumberOfBars({
          followBlock: options.followBlock,
          blockRanges: options.blockRanges,
          nrOfBars: options.nrOfBars,
        }),
      },
      () => ({ sum: 0, count: 0 }),
    );

    // Count the number of statements for each score range
    statements.forEach((statement) => {
      const rawScore = statement.result?.score?.raw ?? 0;
      const index = ScoreIndexHelper.getIndex({
        score: rawScore,
        nrOfBars: options.nrOfBars,
        blocks: options.followBlock ? options.blockRanges : undefined,
      });
      if (index !== undefined) {
        groupData[index].sum += rawScore;
        groupData[index].count += 1;
      }
    });

    // Compute the average score for each group
    const groupAverage: [number, number][] = groupData.map((d) => {
      const avg = d.count > 0 ? (d.sum / Math.max(d.count, 1)) : 0;
      return [avg - 1, avg];
    });

    // Normalize the values to fit the lines onto the bars in the graph
    if (options.graphFitInfoInBar) {
      groupAverage.forEach((score, i) => {
        const newOwnScore = (data[i] / 100) * score[1];
        groupAverage[i] = [newOwnScore - 1, newOwnScore];
      });
    }

    return groupAverage;
  }

  /**
   * Get the score data of the current user
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphReceivedMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @param {number[]} param.data - The data for the general graph. This needs to be used when
   *                                graphFitInfoInBar is set.
   * @returns The score data of the current user
   */
  public static getOwnScore({ options, statements, data }:
  Readonly<InternalOptions & { data: number[] }>): {
      ownScore: [number, number][];
      ownScoreLabel: [number, number] | undefined;
      borderColor: string[] | undefined;
    } {
    if (!options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }
    if (options.followBlock === undefined) {
      throw new Error('followBlock is not defined');
    }

    if (!options.userScore || !options.userStatement) {
      return { ownScore: [], ownScoreLabel: undefined, borderColor: options.barColors };
    }

    // Initialize the data array with zeros
    const ownScore: [number, number][] = Array.from(
      {
        length: NumberOfBarsHelper.getNumberOfBars({
          followBlock: options.followBlock,
          blockRanges: options.blockRanges,
          nrOfBars: options.nrOfBars,
        }),
      },
      () => [0, 0],
    );

    let ownScoreLabel: [number, number] | undefined;
    const borderColor = [...options.barColors ?? []];

    // Count the number of statements for each score range
    statements.forEach((statement) => {
      if (statement === options.userStatement) {
        const rawScore = statement.result?.score?.raw ?? 0;
        const index = ScoreIndexHelper.getIndex({
          score: rawScore,
          nrOfBars: options.nrOfBars,
          blocks: options.followBlock ? options.blockRanges : undefined,
        });
        if (index !== undefined) {
          ownScore[index] = [options.userScore! - 1, options.userScore!];
          ownScoreLabel = [index, options.userScore!];
          if (borderColor !== undefined) {
            borderColor[index] = '#484848';
          }
        }
      }
    });

    // Normalize the values to fit the lines onto the bars in the graph
    if (options.graphFitInfoInBar) {
      ownScore.forEach((score, i) => {
        const newOwnScore = (data[i] / 100) * score[1];
        ownScore[i] = [newOwnScore - 1, newOwnScore];
      });
    }

    return { ownScore, ownScoreLabel, borderColor };
  }
}
