import {
  BarController, BarElement, CategoryScale, Chart, LinearScale,
  Tooltip,
} from 'chart.js';

import BarGraphReceivedMarksInternal from './bar-graph-received-marks.internal';
import { BAR_COLORS } from '../constants/colors';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { BarGraphReceivedMarksOptions } from '../types/bar-graph-received-marks-options';

/**
 * The BarGraphReceivedMarks is a graph type that displays the received marks of a student
 * based on the score verbs.
 */
export default class BarGraphReceivedMarks implements IGraph {
  /** The options for the graph. */
  #options: BarGraphReceivedMarksOptions;

  /** The statements to be displayed in the graph. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the graph.
   * @param {BarGraphReceivedMarksOptions} param.options - The options for the graph.
   */
  constructor(
    { statements, options }:
    Readonly<{ statements: Statement[]; options: BarGraphReceivedMarksOptions }>,
  ) {
    this.#statements = statements;
    this.#options = options;

    this.setDefaults();

    // Register the necessary components for the Chart.js library
    Chart.register(BarController, BarElement, CategoryScale, LinearScale, Tooltip);
  }

  /**
   * Draw the graph
   */
  public draw(): void {
    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'bar',
      data: BarGraphReceivedMarksInternal.getChartData({
        options: this.#options, statements: this.#statements,
      }),
      options: {
        responsive: true,
        maintainAspectRatio: false,
        events: [],
        scales: {
          x: {
            title: {
              display: true, // TODO: make dynamic
              text: this.#options.xAxisLabel,
            },
            stacked: true,
            ticks: {
              autoSkip: false,
            },
          },
          y: {
            title: {
              display: true,
              text: this.#options.yAxisLabel,
            },
            beginAtZero: true,
            position: 'left',
            min: 0,
            max: 100,
          },
          y2: {
            display: this.#options.graphFitInfoInBar,
            title: {
              display: true,
              text: this.#options.y2AxisLabel,
            },
            beginAtZero: true,
            position: 'right',
            min: 0,
            max: 100,
          },
        },
      },
    });
  }

  /**
   * Set the default values for the options
   */
  private setDefaults(): void {
    this.#options.scoreRange = this.#options.scoreRange ?? [0, 100];
    this.#options.showAvg = this.#options.showAvg ?? false;
    this.#options.showAvgLine = this.#options.showAvgLine ?? false;
    this.#options.xAxisLabel = this.#options.xAxisLabel ?? 'Score Range [%]';
    this.#options.yAxisLabel = this.#options.yAxisLabel ?? '% of Group';
    this.#options.y2AxisLabel = this.#options.y2AxisLabel ?? 'Score [%]';
    this.#options.nrOfBars = this.#options.nrOfBars ?? 10;
    this.#options.barColors = this.#options.barColors ?? [...BAR_COLORS];
    this.#options.graphFitInfoInBar = this.#options.graphFitInfoInBar ?? false;
    this.#options.followBlock = this.#options.followBlock ?? false;
    this.#options.averageScoreBarLabel = this.#options.averageScoreBarLabel
      ?? 'Average in this group';
    this.#options.showScore = this.#options.showScore ?? false;
    this.#options.groupScoreLabel = this.#options.groupScoreLabel ?? 'avg score';
    this.#options.showNumberOfParticipants = this.#options.showNumberOfParticipants ?? false;
    this.#options.groupUsersLabel = this.#options.groupUsersLabel ?? 'participants in group';
    this.#options.ownScoreLabel = this.#options.ownScoreLabel ?? 'Your score';
    this.#options.userScoreLabel = this.#options.userScoreLabel ?? 'User score';
  }
}
