import z from 'zod';

import GraphOptionsObject from './graph-options';

const PieChartReceivedMarksOptionsObject = z.intersection(
  GraphOptionsObject,
  z.object({
    backgroundColors: z.string().array().optional().default(['#28c840cc', '#ff5f57cc']),
    borderColors: z.string().array().optional().default(['#28c840', '#ff5f57']),
    correctLabel: z.string().nullish().default('Correct').transform((val) => val ?? 'Correct'),
    dashboardMode: z.boolean().optional().default(false),
    incorrectLabel: z.string().nullish().default('Incorrect').transform(
      (val) => val ?? 'Incorrect',
    ),
  }),
);

export default PieChartReceivedMarksOptionsObject;

/**
 * The options for the pie chart received marks.
 *
 * @property {string[]} backgroundColors - The background colors of the pie chart. Default: [
 *                                        '#28c840cc', '#ff5f57cc']
 * @property {string[]} borderColors - The border colors of the pie chart. Default: [
 *                                    '#28c840', '#ff5f57']
 * @property {string} correctLabel - The label for the correct marks. Default: 'Correct'
 * @property {boolean} dashboardMode - Whether the chart is in dashboard mode. Default: false
 * @property {string} incorrectLabel - The label for the correct marks. Default: 'Incorrect'
 */
export type PieChartReceivedMarksOptions = z
  .infer<typeof PieChartReceivedMarksOptionsObject>;
