import { USER_SELECTED_BORDER_COLOR } from '../constants/colors';
import ColorHelper from '../helpers/color';
import NumberOfBarsHelper from '../helpers/number-of-bars';
import RangeLabelsHelper from '../helpers/range-labels';
import ScoreIndexHelper from '../helpers/score-index';

import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { BarGraphAverageScoreOptions } from '../types/bar-graph-average-score-options';

interface InternalOptions {
  options: BarGraphAverageScoreOptions;
  statements: Statement[];
}

/**
 * The BarGraphAverageScoreInternal class contains helper methods for the
 * BarGraphAverageScore class.
 */
export default class BarGraphAverageScoreInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The chart data
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData {
    const {
      datasets,
      groupAverage,
      ownScore,
      participantsPerGroup,
    } = this.getDatasets({ options, statements });
    const labels = this.getLabels({
      groupAverage, ownScore, options, participantsPerGroup,
    });

    return {
      datasets,
      labels,
    };
  }

  /**
   * Get the labels for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {number[]} param.groupAverage - The average score for each group
   * @param {number[]} [param.ownScore] - The label for the own score and where to place it
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {number[]} param.participantsPerGroup - An array with for each bar the number
   * of participants
   * @returns The labels for the chart
   */
  public static getLabels(
    {
      groupAverage, ownScore, options, participantsPerGroup,
    }:
    Readonly<{
      groupAverage: number[];
      ownScore?: number[];
      options: BarGraphAverageScoreOptions;
      participantsPerGroup: number[];
    }>,
  ): string[][] {
    if (options.nrOfBars === undefined) {
      throw new Error('nrOfBars is not defined');
    }

    const labels = RangeLabelsHelper.getRangeLabels({
      followBlock: options.followBlock,
      blockRanges: options.blockRanges,
      nrOfBars: options.nrOfBars,
      blockNames: options.blockNames,
    });

    // Add the average score to each label for the bars
    labels.map((label, i) => label.push(
      `${options.groupScoreLabel}: ${groupAverage[i].toFixed(2)}`,
    ));

    // Show the score for the current user
    if (ownScore !== undefined && ownScore.length === 2) {
      labels[ownScore[1]].push(`${options.userScoreLabel}: ${ownScore[0]}`);
    }

    // Add the number of participants to each label for the bars
    if (options.showNumberOfParticipants) {
      labels.map((label, i) => label.push(
        `${participantsPerGroup[i] ?? 0} ${options.groupUsersLabel}`,
      ));
    }

    return labels;
  }

  /**
   * Get the datasets for the graph
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The datasets, labels, groupAverage, ownScore, and particpantsPerGroup
   */
  public static getDatasets({ options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset[];
      labels: string[];
      groupAverage: number[];
      ownScore?: number[];
      participantsPerGroup: number[];
    } {
    const datasets: ChartDataset[] = [];
    const backgroundColor = [...options.barColors ?? []];
    let ownScoreLabel;

    const { data: userData, ownScore, ownScoreIndex } = this.getUserData({ options, statements });
    const { data: groupAverage, borderColor, participantsPerGroup } = this.getAverageData({
      options,
      statements,
      ownScoreIndex,
    });

    const isOwnScoreLargerThanAverage = ownScore !== undefined
      && ownScoreIndex !== undefined
      && ownScore > groupAverage[ownScoreIndex];

    datasets.push({
      type: 'bar',
      data: groupAverage,
      label: 'Average Score',
      backgroundColor,
      borderWidth: 5,
      barPercentage: 0.96,
      borderColor,
      order: isOwnScoreLargerThanAverage ? 1 : 3,
      stack: 'stack 0',
    });

    // If there is a score from the user, plot the user bar
    if (ownScore !== undefined && ownScoreIndex !== undefined) {
      datasets.push({
        type: 'bar',
        data: userData,
        label: 'User Score',
        backgroundColor: backgroundColor.map(
          (color) => ColorHelper.newShade({ hexColor: color, magnitude: 40 }),
        ),
        barPercentage: isOwnScoreLargerThanAverage ? 0.96 : 0.90,
        borderColor: backgroundColor.map(
          (color) => ColorHelper.newShade({ hexColor: color, magnitude: 40 }),
        ),
        order: 2,
        stack: groupAverage[ownScoreIndex] !== ownScore ? 'stack 0' : 'stack 1',
      });

      ownScoreLabel = [ownScore, ownScoreIndex];
    }

    return {
      datasets, labels: [], groupAverage, ownScore: ownScoreLabel, participantsPerGroup,
    };
  }

  /**
   * Get the average data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @param {number} param.ownScoreIndex - The index of the user's score in data array.
   * @returns The data for the chart, the border colors, and the number of participants per group
   */
  public static getAverageData({ options, statements, ownScoreIndex }:
  Readonly<InternalOptions & {
    ownScoreIndex?: number;
  }>): { data: number[]; borderColor: string[]; participantsPerGroup: number[] } {
    if (!options.barColors) {
      throw new Error('barColors is not defined');
    }
    if (!options.nrOfBars && !options.blockRanges) {
      throw new Error('nrOfBars and blockRanges are not defined');
    }
    if (!options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }
    if (options.followBlock === undefined) {
      throw new Error('followBlock is not defined');
    }

    // Initialize the data array with zeros
    const groupData = Array.from(
      {
        length: NumberOfBarsHelper.getNumberOfBars({
          followBlock: options.followBlock,
          blockRanges: options.blockRanges,
          nrOfBars: options.nrOfBars,
        }),
      },
      () => ({ sum: 0, count: 0 }),
    );

    const borderColor = [...options.barColors];
    if (ownScoreIndex !== undefined) {
      borderColor[ownScoreIndex] = USER_SELECTED_BORDER_COLOR;
    }

    // Get the sum of the score and the amount of interactions per bar
    statements.forEach((statement) => {
      const rawScore = statement.result?.score?.raw ?? 0;
      const index = ScoreIndexHelper.getIndex({
        score: rawScore,
        nrOfBars: options.nrOfBars,
        blocks: options.followBlock ? options.blockRanges : undefined,
      });
      if (index !== undefined) {
        groupData[index].sum += rawScore;
        groupData[index].count += 1;
      }
    });

    // Average the scores
    const data = groupData.map(({ sum, count }) => (count > 0 ? sum / count : 0));

    // Get the participants per group
    const participantsPerGroup = groupData.map(({ count }) => count);

    return { data, borderColor, participantsPerGroup };
  }

  /**
   * Get the user score data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The data for the chart, the ownScore, and the index for the ownScore
   */
  public static getUserData({ options }:
  Readonly<{
    options: BarGraphAverageScoreOptions;
    statements: Statement[];
  }>): { data: number[]; ownScore?: number; ownScoreIndex?: number } {
    if (!options.barColors) {
      throw new Error('barColors is not defined');
    }
    if (!options.nrOfBars && !options.blockRanges) {
      throw new Error('nrOfBars and blockRanges are not defined');
    }

    let arraySize = 0;
    if (options.blockRanges !== undefined) {
      arraySize = options.blockRanges.length;
    } else if (options.nrOfBars !== undefined) {
      arraySize = options.nrOfBars;
    } else {
      // Should never be here
    }

    // Initialize the data array with zeros
    const data = Array.from({ length: arraySize }, () => (0));

    // TODO: follow block

    // Get the score for the userStatement
    const rawScore = options.userStatement?.result?.score?.raw ?? 0;

    // Get the index for the bar
    const index = ScoreIndexHelper.getIndex({
      score: rawScore,
      nrOfBars: options.nrOfBars,
      blocks: options.followBlock ? options.blockRanges : undefined,
    });

    // Add the score to the data array
    if (index !== undefined) {
      data[index] = rawScore;
    }

    return { data, ownScore: rawScore, ownScoreIndex: index };
  }
}
