import {
  BarController, BarElement, CategoryScale, Chart, LinearScale,
  Tooltip,
} from 'chart.js';

import BarGraphAverageGivenAnswersInternal from './bar-graph-given-answers.internal';
import { BAR_COLORS } from '../constants/colors';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { BarGraphGivenAnswersOptions } from '../types/bar-graph-given-answers-options';

/**
 * The BarGraphGivenAnswers is a graph type that displays the given answers of a student
 * based on the answered verbs.
 */
export default class BarGraphGivenAnswers implements IGraph {
  /** A boolean indicating whether multiple answers are given. */
  #multipleAnswers: boolean;

  /** The options for the graph. */
  #options: BarGraphGivenAnswersOptions;

  /** The statements to be displayed in the graph. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the graph.
   * @param {BarGraphGivenAnswersOptions} param.options - The options for the graph.
   */
  constructor(
    { statements, options }:
    Readonly<{ statements: Statement[]; options: BarGraphGivenAnswersOptions }>,
  ) {
    this.#statements = statements;
    this.#options = options;
    this.#multipleAnswers = statements.findIndex(
      (statement) => (statement.result?.response?.indexOf('[,]') ?? -1) >= 0,
    ) >= 0;

    this.setDefaults();

    // Register the necessary components for the Chart.js library
    Chart.register(BarController, BarElement, CategoryScale, LinearScale, Tooltip);
  }

  /**
   * Draw the graph
   */
  public draw(): void {
    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'bar',
      data: BarGraphAverageGivenAnswersInternal.getChartData({
        multipleAnswers: this.#multipleAnswers,
        options: this.#options,
        statements: this.#statements,
      }),
      options: {
        responsive: true,
        maintainAspectRatio: false,
        events: ['click'],
        scales: {
          x: {
            title: {
              display: true,
              text: this.#options.xAxisLabel,
            },
            beginAtZero: true,
          },
          y: {
            title: {
              display: true,
              text: this.#options.yAxisLabel,
            },
            beginAtZero: true,
            position: 'left',
            min: 0,
            max: 100,
          },
        },
      },
    });
  }

  /**
   * Set the default values for the options
   */
  private setDefaults(): void {
    this.#options.xAxisLabel = this.#options.xAxisLabel ?? 'Answers given';
    this.#options.yAxisLabel = this.#options.yAxisLabel ?? 'Percentage of answers';
    this.#options.barColors = this.#options.barColors ?? [...BAR_COLORS];
  }
}
