import ColorScale from '../helpers/colorScale';
import GoalAnnotationsHelper from '../helpers/goalAnnotations';
import VisitedGroups from '../helpers/visitedGroups';

import type { ChartData, ChartDataset } from 'chart.js';
import type { AnnotationOptions } from 'chartjs-plugin-annotation';
import type { Statement } from 'dashboard-types';

import type { BarGraphCombinedMarksOptions }
  from '../types/bar-graph-combined-marks-options';

interface InternalOptions {
  horizontal: boolean;
  options: BarGraphCombinedMarksOptions;
  statements: Statement[];
}

/**
 * The BarGraphCombinedMarksInternal class contains helper methods for the
 * BarGraphCombinedMarks class.
 */
export default class BarGraphCombinedMarksInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {boolean} param.horizontal - Whether the bar graph is horizontal.
   * @param {BarGraphCombinedMarksOptions} param.options - Additional options for the
   *                                                                bar graph.
   * @param {Statement[]} param.statements - The statements for the bar graph.
   *
   * @returns The chart data, and the goal dataset
   */
  public static getChartData({ horizontal, options, statements }: Readonly<InternalOptions>):
  ChartData & { goalAnnotations: Record<string, AnnotationOptions> } {
    // Get the color scale based on the options
    const colorScale = new ColorScale(options);

    // Get the visited groups with their scores
    const { visitedGroups } = VisitedGroups.getVisitedGroups({ statements });

    return {
      labels: this.getLabels({ visitedGroups }),
      datasets: this.getBarData({ visitedGroups, colorScale }),
      goalAnnotations: GoalAnnotationsHelper.getGoalAnnotations(
        { horizontal, options, visitedGroups },
      ),
    };
  }

  /**
   * Get the labels for the bar graph
   *
   * @param {Object} param - The parameter object.
   * @param {Map<string, number[]>} param.visitedGroups - The visited groups with their scores.
   *
   * @returns The labels for the bar graph.
   */
  public static getLabels({ visitedGroups }:
  Readonly<{ visitedGroups: Map<string, number[]> }>): string[] {
    return [...visitedGroups.keys()];
  }

  /**
   * Get the dataset for the bar graph
   *
   * @param {Object} param - The parameter object.
   * @param {ColorScale} param.colorScale - The color scale for the chart.
   * @param {Map<string, number[]>} param.visitedGroups - The visited groups with their scores.
   *
   * @returns The dataset for the bar graph.
   */
  public static getBarData({ visitedGroups, colorScale }:
  Readonly<{
    colorScale: ColorScale<BarGraphCombinedMarksOptions>;
    visitedGroups: Map<string, number[]>;
  }>):
    ChartDataset<'bar', number[]>[] {
    const dataset = {
      data: [...visitedGroups.values()].map((value) => value[0]),
      backgroundColor: [...visitedGroups.keys()].map(
        (key, i) => colorScale.backgroundColor({ key, index: i }),
      ),
    };
    return [dataset];
  }
}
