import z from 'zod';

import GraphOptionsObject from './graph-options';
import { BAR_COLORS, GOAL_COLOR } from '../constants/colors';

const HorizontalBarGraphWithAveragesOptionsObject = z.intersection(
  GraphOptionsObject,
  z.object({
    barColors: z.string().array().optional().default(BAR_COLORS),
    goalColor: z.string().optional().default(GOAL_COLOR),
    graphColorScale: z.string().optional(),
  }),
);

export default HorizontalBarGraphWithAveragesOptionsObject;

/**
 * The options for the horizontal bar graph with averages.
 *
 * @property {string[]} barColors - The colors for the bars. Default: [
 *                                  '#FF6384', '#4BC0C0', '#FFCD56', '#C9CBCF', '#36A2EB', '#B284BE'
 *                                  ]
 * @property {string} goalColor - The color for the goal line. Default: '#775DD0'
 */
export type HorizontalBarGraphWithAveragesOptions = z
  .infer<typeof HorizontalBarGraphWithAveragesOptionsObject>;
