import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { LineGraphAverageMarksOptions } from '../types/line-graph-average-marks-options';

interface InternalOptions {
  options: LineGraphAverageMarksOptions;
  statements: Statement[];
}

/**
 * The LineGraphAverageMarksInternal class contains helper methods for the
 * LineGraphAverageMarks class.
 */
export default class LineGraphAverageMarksInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {LineGraphAverageMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The chart data
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData {
    const { datasets, labels } = this.getDatasets({ statements, options });

    return {
      datasets,
      labels,
    };
  }

  /**
   * Get the dataset for the graph
   *
   * @param {Object} param - The parameter object.
   * @param {LineGraphAverageMarksOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The datasets for the graph, and the labels
   */
  public static getDatasets({ options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset<'line', number[]>[];
      labels: string[];
    } {
    const { data, labels } = this.getAverageData({ statements });

    const dataset: ChartDataset<'line', number[]> = {
      type: 'line',
      label: 'data',
      backgroundColor: options.barColors,
      borderColor: options.barColors,
      data,
    };

    return { datasets: [dataset], labels };
  }

  /**
   * Get the average data
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The average data of the group
   */
  public static getAverageData({ statements }:
  Readonly<{ statements: Statement[] }>): { data: number[]; labels: string[] } {
    const labels = statements.reduce<Record<number, number>>((acc, statement) => {
      const score = statement.result?.score?.raw ?? 0;
      acc[score] = (acc[score] || 0) + 1;
      return acc;
    }, {});

    const percentageData: Record<number, number> = Object.fromEntries(
      Object.entries(labels).map(([key, value]) => [key, (value / statements.length) * 100]),
    );

    return {
      data: Object.values(percentageData),
      labels: Object.keys(percentageData)
        .map((label) => label.replace(/_/g, ' ')),
    };
  }
}
