import z from 'zod';

import GraphOptionsObject from './graph-options';

const RadarChartOpinionOptionsObject = z.intersection(
  GraphOptionsObject,
  z.object({
    /** @property {number} [textSize=12] - The size of the font in the graph */
    textSize: z.number().optional().default(12),
  }),
);

export default RadarChartOpinionOptionsObject;

export type RadarChartOpinionOptions = z
  .infer<typeof RadarChartOpinionOptionsObject>;
