import {
  BarController, BarElement, CategoryScale, Chart, LinearScale,
  Tooltip,
} from 'chart.js';

import BarGraphAverageScoreInternal from './bar-graph-average-score.internal';
import { BAR_COLORS } from '../constants/colors';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { BarGraphAverageScoreOptions } from '../types/bar-graph-average-score-options';

/**
 * The BarGraphAverageScore is a graph type that displays the TODO: add more comment
 */
export default class BarGraphAverageScore implements IGraph {
  /** The options for the graph. */
  #options: BarGraphAverageScoreOptions;

  /** The statements to be displayed in the graph. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the graph.
   * @param {BarGraphAverageScoreOptions} param.options - The options for the graph.
   */
  constructor(
    { statements, options }:
    Readonly<{ statements: Statement[]; options: BarGraphAverageScoreOptions }>,
  ) {
    this.#statements = statements;
    this.#options = options;

    this.setDefaults();

    // Register the necessary components for the Chart.js library
    Chart.register(BarController, BarElement, CategoryScale, LinearScale, Tooltip);
  }

  /**
   * Draws a bar graph on the provided canvas context using the Chart.js library.
   *
   * This method initializes and renders a bar chart with the given data and configuration options.
   * It supports stacked and non-stacked bar graphs, responsive resizing, and custom axis labels.
   * The graph is interactive, responding to click events.
   *
   * ### Example Usage:
   *
   * ```ts
   * import { BarGraphAverageScore } from './bar-graph-average-score';
   *
   * const canvas = document.getElementById('myChart') as HTMLCanvasElement;
   * const ctx = canvas.getContext('2d');
   *
   * const options = {
   *   ctx: ctx,
   *   yAxisLabel: 'Average Score (%)',
   * };
   *
   * const statements = [
   *   { label: 'Statement 1', ownScore: 85, groupAvg: 75 },
   *   { label: 'Statement 2', ownScore: 90, groupAvg: 80 },
   *   { label: 'Statement 3', ownScore: 70, groupAvg: 65 },
   * ];
   *
   * const barGraph = new BarGraphAverageScore(options, statements);
   * barGraph.draw();
   * ```
   *
   * ### Expected Output:
   * - A bar graph is rendered on the canvas with the provided data.
   * - The x-axis displays the labels for each statement.
   * - The y-axis shows the percentage scores ranging from 0 to 100.
   * - Bars are displayed for both individual scores (`ownScore`) and group averages (`groupAvg`).
   * - The graph is responsive and adjusts its size based on the container.
   *
   * ### Notes:
   * - The `stacked` property for the x-axis is dynamically set based on the comparison of
   * `ownScore` and `groupAvg`.
   * - Ensure that the `ctx` provided in the options is a valid 2D rendering context from a
   * `<canvas>` element.
   * - The `statements` array should contain objects with `label`, `ownScore`, and `groupAvg`
   * properties.
   */
  public draw(): void {
    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'bar',
      data: BarGraphAverageScoreInternal.getChartData(
        { options: this.#options, statements: this.#statements },
      ),
      options: {
        responsive: true,
        maintainAspectRatio: false,
        events: ['click'],
        scales: {
          x: {
            stacked: true,
            ticks: {
              autoSkip: false,
            },
          },
          y: {
            title: {
              display: true,
              text: this.#options.yAxisLabel,
            },
            stacked: false,
            beginAtZero: true,
            position: 'left',
            min: 0,
            max: 100,
          },
        },
        plugins: {
          legend: {
            display: false,
          },
          tooltip: {
            enabled: true,
          },
        },
      },
    });
  }

  /**
   * Set the default values for the options
   */
  private setDefaults(): void {
    this.#options.barColors = this.#options.barColors ?? [...BAR_COLORS];
    this.#options.followBlock = this.#options.followBlock ?? false;
    this.#options.groupScoreLabel = this.#options.groupScoreLabel ?? 'avg score';
    this.#options.groupUsersLabel = this.#options.groupUsersLabel ?? 'participants in group';
    this.#options.nrOfBars = this.#options.nrOfBars ?? 10;
    this.#options.showNumberOfParticipants = this.#options.showNumberOfParticipants ?? false;
    this.#options.userScoreLabel = this.#options.userScoreLabel ?? 'User score';
    this.#options.yAxisLabel = this.#options.yAxisLabel ?? 'Average score of all attempts';
  }
}
