import {
  _adapters,
  CategoryScale,
  Chart,
  LinearScale,
  LineController,
  LineElement,
  PointElement,
  TimeScale,
  Tooltip,
} from 'chart.js';

import LineGraphOwnMarksInternal from './line-graph-own-marks.internal';
import { BAR_COLORS } from '../constants/colors';
import StdDateAdapter from '../helpers/date-adapter';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { LineGraphOwnMarksOptions } from '../types/line-graph-own-marks-options';

/**
 * The LineGraphOwnMarks is a graph type that displays the user's own marks
 * based on their individual score data.
 */
export default class LineGraphOwnMarks implements IGraph {
  /** The options for the graph. */
  #options: LineGraphOwnMarksOptions;

  /** The statements to be displayed in the graph. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the graph.
   * @param {LineGraphOwnMarksOptions} param.options - The options for the graph.
   */
  constructor(
    { statements, options }:
    Readonly<{ statements: Statement[]; options: LineGraphOwnMarksOptions }>,
  ) {
    this.#statements = statements;
    this.#options = options;

    this.setDefaults();

    // Register the necessary components for the Chart.js library
    Chart.register(
      LineController,
      TimeScale,
      LineElement,
      PointElement,
      CategoryScale,
      LinearScale,
      Tooltip,
      TimeScale,
    );

    // eslint-disable-next-line no-underscore-dangle
    _adapters._date.override(StdDateAdapter.chartJsStandardAdapter());
  }

  /**
   * Draws a line graph on the provided canvas context using the Chart.js library.
   *
   * This method initializes and renders a line chart with the given data and configuration options.
   * It visualizes the distribution of marks received by students, grouped into score ranges,
   * and displays the percentage of the group that falls into each range.
   *
   * ### Example Usage:
   *
   * ```ts
   * import { LineGraphOwnMarks } from './line-graph-own-marks';
   *
   * const canvas = document.getElementById('myChart') as HTMLCanvasElement;
   * const ctx = canvas.getContext('2d');
   *
   * const options = {
   *   ctx: ctx,
   *   xAxisLabel: 'Score Range [%]',
   *   yAxisLabel: '% of Group',
   * };
   *
   * const statements = [
   *   { scoreRange: '0-10', percentage: 5 },
   *   { scoreRange: '11-20', percentage: 10 },
   *   { scoreRange: '21-30', percentage: 15 },
   *   { scoreRange: '31-40', percentage: 20 },
   *   { scoreRange: '41-50', percentage: 25 },
   *   { scoreRange: '51-60', percentage: 15 },
   *   { scoreRange: '61-70', percentage: 10 },
   * ];
   *
   * const lineGraph = new LineGraphOwnMarks({ options, statements });
   * lineGraph.draw();
   * ```
   *
   * ### Expected Output:
   * - A line graph is rendered on the canvas with the provided data.
   * - The x-axis displays the score ranges (e.g., "0-10", "11-20").
   * - The y-axis shows the percentage of the group that falls into each score range.
   * - A smooth line connects the data points, representing the user's own marks over time or
   * categories.
   * - The graph is responsive and adjusts its size based on the container.
   *
   * ### Notes:
   * - Ensure that the `ctx` provided in the options is a valid 2D rendering context from a
   * `<canvas>` element.
   * - The `statements` array should contain objects with `scoreRange` and `percentage` properties.
   * - The graph is interactive and responds to click events.
   */
  public draw(): void {
    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'line',
      data: LineGraphOwnMarksInternal.getChartData({
        options: this.#options, statements: this.#statements,
      }),
      options: {
        responsive: true,
        maintainAspectRatio: false,
        events: ['click'],
        scales: {
          x: {
            type: 'time',
            time: {
              unit: 'day',
              tooltipFormat: 'll',
            },
            title: {
              display: true,
              text: this.#options.xAxisLabel,
            },
            min: this.earliestStatement().getTime(),
            max: Date.now(),
          },
          y: {
            title: {
              display: true,
              text: this.#options.yAxisLabel,
            },
            beginAtZero: true,
            position: 'left',
            min: 0,
            max: 10,
            ticks: {
              stepSize: 2,
            },
          },
        },
      },
    });
  }

  /**
   * Set the default values for the options
   */
  private setDefaults(): void {
    this.#options.barColors = this.#options.barColors ?? [...BAR_COLORS];
    this.#options.xAxisLabel = this.#options.xAxisLabel ?? 'Date';
    this.#options.yAxisLabel = this.#options.yAxisLabel ?? 'Grade [0-10]';
  }

  private earliestStatement(): Date {
    const earliestStatement = this.#statements.reduce((earliest, current) => (
      new Date(current.timestamp ?? Date.now()) < new Date(earliest.timestamp ?? Date.now())
        ? current
        : earliest
    ), this.#statements[0]);
    return new Date(
      new Date(earliestStatement.timestamp ?? Date.now()).getTime() - 1 * 24 * 60 * 60 * 1000,
    );
  }
}
