export default class NumberOfBarsHelper {
  /**
   * Determines the number of bars to display in a dashboard graph based on the provided
   * configuration.
   *
   * This function calculates the number of bars by either using the length of predefined
   * block ranges
   * (if `followBlock` is enabled) or falling back to a default number of bars (`nrOfBars`).
   *
   * @param {Object} params - The parameters for determining the number of bars.
   * @param {boolean} params.followBlock - A flag indicating whether to use predefined block ranges.
   *                                        If true, the number of bars will be based on the length
   *                                        of `blockRanges`.
   * @param {string[] | undefined} params.blockRanges - An optional array of block range strings
   *                                                    (e.g., ["0,10", "10,20"]). Required if
   *                                                    `followBlock` is true.
   * @param {number} params.nrOfBars - The default number of bars to use if `followBlock` is false
   *                                   or `blockRanges` is undefined.
   * @returns {number} The calculated number of bars for the graph.
   *
   * @example
   * // Example 1: Using predefined block ranges
   * const numberOfBars = NumberOfBarsHelper.getNumberOfBars({
   *   followBlock: true,
   *   blockRanges: ["0,10", "10,20", "20,30"],
   *   nrOfBars: 5,
   * });
   * // Output: 3 (based on the length of blockRanges)
   *
   * @example
   * // Example 2: Using default number of bars
   * const numberOfBars = NumberOfBarsHelper.getNumberOfBars({
   *   followBlock: false,
   *   blockRanges: undefined,
   *   nrOfBars: 5,
   * });
   * // Output: 5 (based on the default nrOfBars)
   *
   * @example
   * // Example 3: Handling undefined blockRanges with followBlock disabled
   * const numberOfBars = NumberOfBarsHelper.getNumberOfBars({
   *   followBlock: false,
   *   blockRanges: undefined,
   *   nrOfBars: 4,
   * });
   * // Output: 4 (default nrOfBars is used)
   *
   * @example
   * // Example 4: Handling undefined blockRanges with followBlock enabled
   * const numberOfBars = NumberOfBarsHelper.getNumberOfBars({
   *   followBlock: true,
   *   blockRanges: undefined,
   *   nrOfBars: 6,
   * });
   * // Output: 6 (default nrOfBars is used because blockRanges is undefined)
   */
  public static getNumberOfBars({
    followBlock,
    blockRanges,
    nrOfBars,
  }:
  Readonly<{
    followBlock: boolean;
    blockRanges: string[] | undefined;
    nrOfBars: number;
  }>): number {
    if (!followBlock || blockRanges === undefined) {
      return nrOfBars;
    }

    return blockRanges.length;
  }
}
