import type { Statement } from 'dashboard-types';

export default class VisitedGroups {
  /**
     * Get the visitedGroups for the statements
     *
     * @param {Object} param - The parameter object.
     * @param {Statement[]} param.statements - The statements for the chart.
     *
     * @returns A Map with the scores for each group and a lookup map to quickly get the index.
     */
  public static getVisitedGroups({ statements }:
  Readonly<{ statements: Statement[] }>): {
      visitedGroups: Map<string, number[]>;
      visitedGroupsEntries: Map<string, number>;
    } {
    // Create a Map that contains all groups and the raw scores
    const visitedGroups = new Map<string, number[]>();

    // Create a Map that contains the indexes of the items in the Map for quick lookup
    const visitedGroupsEntries = new Map<string, number>();

    // Index to fill the entries Map
    let index = 0;

    statements.filter(
      // Make sure a grouping is defined on the context activities
      (statement) => statement.context?.contextActivities?.grouping !== undefined,
    ).forEach(
      (statement) => {
        // Get the en-US name of the grouping
        const group = statement
          .context?.contextActivities?.grouping?.[0].definition?.name?.['en-US'];

        if (group !== undefined && !visitedGroups.has(group)) {
          // If visited groups does not contain the group yet, push the score as an array
          visitedGroups.set(group, [statement.result?.score?.raw ?? 0]);

          // Update our entries lookup Map
          visitedGroupsEntries.set(group, index);
          index += 1;
        } else if (group !== undefined) {
          // If the visited group already has the group, push the score to the existing array
          visitedGroups.get(group)?.push(statement.result?.score?.raw ?? 0);
        }
      },
    );

    return { visitedGroups, visitedGroupsEntries };
  }
}
