import {
  BarController, BarElement, CategoryScale, Chart, Legend, LinearScale,
  Tooltip,
} from 'chart.js';
import annotationPlugin from 'chartjs-plugin-annotation';

import HorizontalBarGraphWithAveragesInternal from './horizontal-bar-graph-with-averages.internal';
import HorizontalBarGraphWithAveragesOptionsObject
  from '../types/horizontal-bar-graph-with-averages-options';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { HorizontalBarGraphWithAveragesOptions }
  from '../types/horizontal-bar-graph-with-averages-options';

/**
 * The HorizontalBarGraphWithAverages is a graph type that displays the average scores
 * in a horizontal bar chart format
 */
export default class HorizontalBarGraphWithAverages implements IGraph {
  /** The classLabels for the chart */
  #classLabels: string[];

  /** The options for the chart. */
  #options: HorizontalBarGraphWithAveragesOptions;

  /** The statements to be displayed in the chart. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {string[]} param.classLabels - The labels for the classes.
   * @param {Statement[]} param.statements - The statements to be displayed in the chart.
   * @param {HorizontalBarGraphWithAveragesOptions} param.options - The options for the chart.
   */
  constructor(
    {
      classLabels, statements, options,
    }:
    Readonly<{
      classLabels: string[];
      statements: Statement[];
      options: HorizontalBarGraphWithAveragesOptions;
    }>,
  ) {
    this.#classLabels = classLabels;
    this.#statements = statements;
    // Parse and set the options using the provided options or default values
    this.#options = HorizontalBarGraphWithAveragesOptionsObject.parse(options);

    // Register the necessary components for the Chart.js library
    Chart.register(
      BarController,
      BarElement,
      CategoryScale,
      Legend,
      LinearScale,
      Tooltip,
      annotationPlugin,
    );
  }

  public draw(): void {
    const { labels, datasets, goalAnnotations } = HorizontalBarGraphWithAveragesInternal
      .getChartData({
        options: this.#options,
        statements: this.#statements,
      });

    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'bar',
      data: {
        labels,
        datasets,
      },
      options: {
        transitions: {
          show: {
            animations: {
              x: {
                from: 0,
              },
              y: {
                from: 0,
              },
            },
          },
        },
        indexAxis: 'y', // This makes the bar chart horizontal
        responsive: true,
        maintainAspectRatio: false,
        events: ['click'],
        scales: {
          x: {
            stacked: false,
            beginAtZero: true,
            position: 'bottom',
            min: 0,
            max: 100,
          },
          y: {
            stacked: true,
            ticks: {
              autoSkip: false,
            },
          },
        },
        plugins: {
          legend: {
            display: true,
            position: 'bottom',
            labels: {
              generateLabels: () => [{
                text: 'Group average',
                fillStyle: this.#options.goalColor,
                strokeStyle: this.#options.goalColor,
                lineWidth: 3,
                hidden: false,
                index: 0,
              }],
            },
          },
          tooltip: {
            enabled: true,
          },
          annotation: {
            annotations: goalAnnotations,
          },
        },
      },
    });
  }
}
