import { USER_SELECTED_BORDER_COLOR } from '../constants/colors';

import type { ChartData, ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { BarGraphGivenAnswersOptions } from '../types/bar-graph-given-answers-options';

interface InternalOptions {
  multipleAnswers: boolean;
  options: BarGraphGivenAnswersOptions;
  statements: Statement[];
}

/**
 * The BarGraphAverageGivenAnswersInternal class contains helper methods for the
 * BarGraphAverageGivenAnswers class.
 */
export default class BarGraphAverageGivenAnswersInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {boolean} param.multipleAnswers - Indicates if there are multiple answers.
   * @param {BarGraphGivenAnswersOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The chart data
   */
  public static getChartData({ multipleAnswers, options, statements }:
  Readonly<InternalOptions>): ChartData {
    const {
      datasets, labels,
    } = this.getDatasets({ multipleAnswers, options, statements });

    return {
      datasets,
      labels,
    };
  }

  /**
   * Get the datasets for the graph
   *
   * @param {Object} param - The parameter object.
   * @param {boolean} param.multipleAnswers - Indicates if there are multiple answers.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The datasets for the graph
   */
  public static getDatasets({ multipleAnswers, options, statements }:
  Readonly<InternalOptions>): {
      datasets: ChartDataset[];
      labels: string[];
    } {
    const datasets: ChartDataset[] = [];
    const backgroundColor = [...options.barColors ?? []];

    const { data, labels, borderColor } = this.getData({ multipleAnswers, options, statements });
    datasets.push({
      type: 'bar',
      data,
      backgroundColor,
      borderWidth: 5,
      borderColor,
    });

    return {
      datasets, labels,
    };
  }

  /**
   * Get the data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {boolean} param.multipleAnswers - Indicates if there are multiple answers.
   * @param {BarGraphAverageScoreOptions} param.options - Additional options for the bar graph.
   * @param {Statement[]} param.statements - The statements for the graph.
   * @returns The data for the chart, the labels, and the border colors
   */
  public static getData({ multipleAnswers, options, statements }:
  Readonly<InternalOptions>): { data: number[]; labels: string[]; borderColor: string[] } {
    if (!options.barColors) {
      throw new Error('barColors is not defined');
    }
    const answerCounts: Record<string, number> = {};
    const borderColor = [...options.barColors];

    // Initialize answerCounts with 0 for each choice (if there are any)
    statements.forEach((statement) => {
      if (statement?.object?.definition?.choices) {
        statement.object.definition.choices.forEach((choice) => {
          answerCounts[choice.id] = 0;
        });
      }
    });

    let totalAnswers = 0;
    if (multipleAnswers) {
      // Count the answers given per answer
      statements.forEach((statement) => {
        // Treat a response that has multiple answers as separate responses
        const responses = statement.result?.response?.split('[,]');
        if (responses === undefined) {
          return;
        }
        responses.forEach((response) => {
          if (Object.hasOwn(answerCounts, response)) {
            answerCounts[response] += 1;
            totalAnswers += 1;
          } else {
            if (response.trim() !== '') {
              answerCounts[response] = 1;
                totalAnswers += 1;
            }
          }
        });
      });
    } else {
      totalAnswers = statements.length();
      // Count the answers given per answer
      statements.forEach((statement) => {
        const response = statement.result?.response;
        if (response === undefined) {
          return;
        }
        if (Object.hasOwn(answerCounts, response)) {
          answerCounts[response] += 1;
        } else {
          answerCounts[response] = 1;
        }
      });
    }

    Object.keys(answerCounts).forEach((key: string) => {
      const nkey = key.replace(/_/g, ' ');
      if (key !== nkey) {
        answerCounts[nkey] = answerCounts[key];
        delete answerCounts[key];
      }
    });
    const labels = Object.keys(answerCounts)
      .map((label) => label);
    const data = labels.map((label) => (answerCounts[label] / totalAnswers) * 100);

    // Highlight the user's selected answer(s) with a different border color
    labels.forEach((label, index) => {
      if (
        multipleAnswers && (options.userAnswer?.split('[,]') ?? []).filter(
          (answer) => answer === label,
        ).length > 0
      ) {
        borderColor[index] = USER_SELECTED_BORDER_COLOR;
      }
      if (label === options.userAnswer) {
        borderColor[index] = USER_SELECTED_BORDER_COLOR;
      }
    });

    return { data, labels, borderColor };
  }
}
