import {
  ArcElement, Chart, Legend, PolarAreaController, RadialLinearScale, Title, Tooltip,
} from 'chart.js';

import PolarAreaChartSubjectOverviewInternal from './polar-area-chart-subject-overview.internal';
import PolarAreaChartSubjectOverviewOptionsObject
  from '../types/polar-area-chart-subject-overview-options';

import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { PolarAreaChartSubjectOverviewOptions }
  from '../types/polar-area-chart-subject-overview-options';

/**
 * The PolarAreaChartSubjectOverview is a chart type that displays the received marks
 * as a polar chart.
 */
export default class PolarAreaChartSubjectOverview implements IGraph {
  /** The options for the chart. */
  #options: PolarAreaChartSubjectOverviewOptions;

  /** The statements to be displayed in the chart. */
  #statements: Statement[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements to be displayed in the chart.
   * @param {PolarAreaChartSubjectOverviewOptions} param.options - The options for the chart.
   */
  constructor(
    {
      statements, options,
    }:
    Readonly<{
      statements: Statement[];
      options: PolarAreaChartSubjectOverviewOptions;
    }>,
  ) {
    this.#statements = statements;
    // Parse and set the options using the provided options or default values
    this.#options = PolarAreaChartSubjectOverviewOptionsObject.parse(options);

    // Register the necessary components for the Chart.js library
    Chart.register(
      PolarAreaController,
      ArcElement,
      RadialLinearScale,
      Legend,
      Tooltip,
      Title,
    );
  }

  public draw(): void {
    try {
      const {
        labels,
        datasets,
        legendLabels,
      } = PolarAreaChartSubjectOverviewInternal.getChartData({
        options: this.#options,
        statements: this.#statements,
      });

      // eslint-disable-next-line no-new
      new Chart(this.#options.ctx, {
        type: 'polarArea',
        data: { labels, datasets },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: true,
              position: 'top',
              labels: {
                generateLabels: () => legendLabels,
              },
            },
            tooltip: {
              intersect: false,
              enabled: true,
            },
          },
        },
      });
    } catch (error) {
      throw Error(`Error drawing polar-area-chart-subject-overview chart: ${error}`);
    }
  }
}
