import {
  Chart, Filler, Legend, LineElement,
  PointElement, RadarController, RadialLinearScale, Tooltip,
} from 'chart.js';

import RadarChartOpinionInternal from './radar-chart-opinion.internal';
import RadarChartOpinionOptionsObject from '../types/radar-chart-opinion-options';

import type { ChartDataset } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type IGraph from '../interfaces/IGraph';
import type { RadarChartOpinionOptions } from '../types/radar-chart-opinion-options';
import type { StatementGraph } from '../types/statement-graph';

/**
 * The RadarChartOpinion is a chart type that displays the opinion page answers
 * as a radar chart.
 */
export default class RadarChartOpinion implements IGraph {
  /** The classLabels for the chart */
  #classLabels: string[];

  /** The datasets for the chart. */
  #datasets: ChartDataset<'radar', number[]>[];

  /** The options for the chart. */
  #options: RadarChartOpinionOptions;

  /** The statements to be displayed in the chart. */
  #statements: (Statement & StatementGraph)[];

  /**
   *
   * @param {Object} param - The parameter object.
   * @param {string[]} param.classLabels - The labels for the classes.
   * @param {ChartDataset<'radar', number[]>[]} param.datasets - The datasets for the chart.
   * @param {Statement[]} param.statements - The statements to be displayed in the chart.
   * @param {RadarChartOpinionOptions} param.options - The options for the chart.
   */
  constructor(
    {
      classLabels, datasets, statements, options,
    }:
    Readonly<{
      classLabels: string[];
      datasets: ChartDataset<'radar', number[]>[];
      statements: (Statement & StatementGraph)[];
      options: RadarChartOpinionOptions;
    }>,
  ) {
    this.#classLabels = classLabels;
    this.#datasets = datasets;
    this.#statements = statements;
    // Parse and set the options using the provided options or default values
    this.#options = RadarChartOpinionOptionsObject.parse(options);

    // Register the necessary components for the Chart.js library
    Chart.register(
      RadarController,
      PointElement,
      LineElement,
      RadialLinearScale,
      Legend,
      Tooltip,
      Filler,
    );
  }

  public draw(): void {
    try {
      RadarChartOpinionInternal.getChartData({
        options: this.#options,
        statements: this.#statements,
      });

      // eslint-disable-next-line no-new
      new Chart(this.#options.ctx, {
        type: 'radar',
        data: {
          labels: this.#classLabels,
          datasets: this.#datasets ? this.#datasets : [],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            r: {
              beginAtZero: true,
              max: 100,
              pointLabels: {
                font: {
                  size: this.#options.textSize,
                },
              },
            },
          },
          plugins: {
            legend: {
              display: true,
              position: 'top',
            },
            tooltip: {
              enabled: true,
            },
          },
        },
      });
    } catch (error) {
      throw Error(`Error drawing radar chart: ${error}`);
    }
  }
}
