import UnknownFormat from '../types/errors/unknown-format';

import type { AnnotationOptions } from 'chartjs-plugin-annotation';

/**
 * Interface used to constrain the generic type T in getGoalAnnotations.
 * It includes properties for the tracking score and the goal color..
 * - `trackingPassed`: The parameter given to see what the passed score should be.
 * - `goalColor`: The color for the goal line.
 */
interface GoalAnnotationsParams {
  trackingPassed: string | null;
  goalColor: string;
}

export default class GoalAnnotationsHelper {
  /**
   * Get the goal score based from the tracking passed paramter
   *
   * @param trackingPassed - The tracking passed parameter from the options
   *
   * @returns The goal score
   */
  public static goalScore(trackingPassed: string | null): number {
    // Default to 55% if no tracking passed is provided
    if (trackingPassed == null) {
      return 55;
    }

    // If the tracking passed is a percentage and between 0 and 100, return it as an integer
    if (
      trackingPassed.includes('%')
      && parseInt(trackingPassed, 10) >= 0
      && parseInt(trackingPassed, 10) <= 100
    ) {
      return parseInt(trackingPassed, 10);
    }

    // If the tracking passed is a decimal between 0 and 1, convert it to a percentage
    if (parseFloat(trackingPassed) >= 0 && parseFloat(trackingPassed) <= 1) {
      return Math.round(parseFloat(trackingPassed) * 100.0);
    }

    throw new UnknownFormat(`
      The given tracking passed format is unknown: ${trackingPassed}. Please provide null, a
      percentage between 0 and 100 (e.g. 55%) or a decimal between 0 and 1 (e.g. 0.55).
    `);
  }

  /**
   * Get the data for the goal line
   *
   * @param {Object} param - The parameter object.
   * @param {HorizontalBarGraphWithAveragesOptions} param.options - Additional options for the
   *                                                                bar graph.
   * @param {Map<string, number[]>} param.visitedGroups - The visited groups with their scores.
   *
   * @return The dataset for the goal line
   */
  public static getGoalAnnotations<T extends GoalAnnotationsParams>(
    { horizontal, options, visitedGroups }: Readonly<{
      horizontal: boolean;
      options: T;
      visitedGroups: Map<string, number[]>;
    }>,
  ): Record<string, AnnotationOptions> {
    // Get the goal score based on the tracking passed option
    const goalScore = this.goalScore(options.trackingPassed);

    const annotations: Record<string, AnnotationOptions> = {};

    [...visitedGroups.keys()].forEach((_, i) => {
      annotations[`goalLine-${i}`] = {
        type: 'line',
        xMin: horizontal ? goalScore : i - 0.4,
        xMax: horizontal ? goalScore : i + 0.4,
        yMin: horizontal ? i - 0.4 : goalScore,
        yMax: horizontal ? i + 0.4 : goalScore,
        borderColor: options.goalColor,
        borderWidth: 5,
      };
    });

    return annotations;
  }
}
