import z from 'zod';

import type { ChartItem } from 'chart.js';

const GraphOptionsObject = z.object({
  ctx: z.custom<ChartItem>().nonoptional(),
});

export default GraphOptionsObject;

/**
 * Common options for the graphs.
 *
 * @property ctx - The context for the graph.
 */
export type GraphOptions = z.infer<typeof GraphOptionsObject>;
