import ColorScale from '../helpers/colorScale';
import VisitedGroups from '../helpers/visitedGroups';

import type { ChartData, ChartDataset, Color } from 'chart.js';
import type { Statement } from 'dashboard-types';

import type { PolarAreaChartSubjectOverviewOptions }
  from '../types/polar-area-chart-subject-overview-options';

export interface Question {
  question: [string[], number][] | undefined;
  groupName: string | undefined;
}

interface LegendLabel {
  text: string;
  fillStyle: Color;
}

interface InternalOptions {
  options: PolarAreaChartSubjectOverviewOptions;
  statements: Statement[];
}

/**
 * The PieChartReceivedMarksInternal class contains helper methods for the
 * PieChartReceivedMarks class.
 */
export default class PolarAreaChartSubjectOverviewInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {PolarAreaChartSubjectOverviewOptions} param.options - Additional options for the chart.
   * @param {Statement[]} param.statements - The statements for the chart.
   *
   * @returns The chart dataset and legend labels.
   */
  public static getChartData({ options, statements }:
  Readonly<InternalOptions>): ChartData & { legendLabels: LegendLabel[] } {
    // Get the color scale based on the options
    const colorScale = new ColorScale(options);

    // Get the visited groups with their scores
    const { visitedGroups, visitedGroupsEntries } = VisitedGroups.getVisitedGroups({ statements });

    const { questions } = this.getQuestions({ statements, visitedGroups, visitedGroupsEntries });

    const { datasets } = this.getDatasets({ colorScale, questions, visitedGroupsEntries });
    const { labels } = this.getLabels({ questions });
    const { legendLabels } = this.getLegendLabels({ colorScale, visitedGroupsEntries });

    return {
      labels,
      datasets,
      legendLabels,
    };
  }

  /**
   * Get the labels for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {Question[]} param.questions - The questions for the chart.
   *
   * @returns The chart labels.
   */
  public static getLabels({ questions }:
  Readonly<{ questions: Question[] }>): { labels: string[] } {
    // Flatmap the single questions in the questions into a list of labels based on the first
    // value in the string array
    const labels = questions.flatMap(
      ((question) => question.question?.map((singleQuestion) => singleQuestion[0][0]) ?? []),
    );
    return { labels };
  }

  /**
   * Get the labels for the legend of the chart
   *
   * @param {Object} param - The parameter object.
   * @param {ColorScale} param.colorScale - The color scale for the chart.
   * @param {Map<string, number>} param.visitedGroupsEntries - The lookup map for the groups in the
   *                                                         chart.
   * @returns The legend labels for the chart.
   */
  public static getLegendLabels({ colorScale, visitedGroupsEntries }:
  Readonly<{
    colorScale: ColorScale<PolarAreaChartSubjectOverviewOptions>;
    visitedGroupsEntries: Map<string, number>;
  }>): { legendLabels: LegendLabel[] } {
    const legendLabels: LegendLabel[] = [];

    // Go over the visited group lookup table, and take the labels for the text element
    // of the legend label, and use the 'position' to get the color from the color array.
    // We make sure to get the mod for the length of the background colors, for when we
    // have more groups than colors.
    visitedGroupsEntries.forEach(
      (value, key) => legendLabels.push(
        { text: key, fillStyle: colorScale.backgroundColor({ key, index: value }) },
      ),
    );

    return { legendLabels };
  }

  /**
   * Get the datasets for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {ColorScale} param.colorScale - The color scale for the chart.
   * @param {Question[]} param.questions - The questions for the chart.
   * @param {Map<string, number>} param.visitedGroupsEntries - The lookup map for the groups in the
   *                                                         chart.
   * @returns The datasets.
   */
  public static getDatasets({
    colorScale, questions, visitedGroupsEntries,
  }:
  Readonly<{
    colorScale: ColorScale<PolarAreaChartSubjectOverviewOptions>;
    questions: Question[];
    visitedGroupsEntries: Map<string, number>;
  }>): {
      datasets: ChartDataset<'polarArea', number[]>[];
    } {
    const { data, backgroundColors } = this.getData({
      colorScale, questions, visitedGroupsEntries,
    });

    const dataset = {
      data,
      backgroundColor: backgroundColors,
    };

    return { datasets: [dataset] };
  }

  /**
   * Get the data for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {ColorScale} param.colorScale - The color scale for the chart.
   * @param {Question[]} param.questions - The questions object of the grouping.
   * @param {Map<string, number>} param.visitedGroupsEntries - The lookup map for the groups in the
   *                                                         chart.
   * @returns The data and the background colors based on the visitedGroupsEntries index.
   */
  public static getData({
    colorScale, questions, visitedGroupsEntries,
  }:
  Readonly<{
    colorScale: ColorScale<PolarAreaChartSubjectOverviewOptions>;
    questions: Question[];
    visitedGroupsEntries: Map<string, number>;
  }>): {
      data: number[]; backgroundColors: string[];
    } {
    const data: number[] = [];
    const backgroundColors: string[] = [];

    questions.forEach(
      ((question) => {
        question.question?.forEach((singleQuestion) => {
          data.push(singleQuestion[1]);
          const index = visitedGroupsEntries.get(question.groupName ?? '') ?? 0;
          backgroundColors.push(colorScale.backgroundColor({ key: question.groupName, index }));
        });
      }
      ),
    );

    return { data, backgroundColors };
  }

  /**
   * Get the questions for the chart
   *
   * @param {Object} param - The parameter object.
   * @param {Statement[]} param.statements - The statements for the chart.
   * @param {Map<string, number[]>} param.visitedGroups - The groups for this chart.
   * @param {Map<string, number>} param.visitedGroupsEntries - The lookup map for the groups in the
   *                                                         chart.
   * @returns The questions.
   */
  public static getQuestions({
    statements, visitedGroups, visitedGroupsEntries,
  }:
  Readonly<{
    statements: Statement[];
    visitedGroups: Map<string, number[]>;
    visitedGroupsEntries: Map<string, number>;
  }>): { questions: Question[] } {
    const questions = statements.map(
      (statement) => ({
        question: statement.result?.extensions?.['http://xerte.org.uk/result/grouping/scores'],
        groupName: statement.context?.contextActivities?.grouping?.[0].definition?.name?.['en-US'],
      }) as Question,
    );

    // Create a new array to store the questions in the order of the visited groups
    const sortedQuestions = new Array<Question>(visitedGroups.size);

    questions.forEach((question) => {
      if (question.groupName !== undefined) {
        // Get the index of the question in the visitedGroups via the lookup Map
        const index = visitedGroupsEntries.get(question.groupName);

        if (index !== undefined) {
          sortedQuestions[index] = question;
        }
      }
    });

    // Return the sorted questions
    return { questions: sortedQuestions };
  }
}
