import type { ChartDataset } from 'chart.js';
import type { AnnotationOptions } from 'chartjs-plugin-annotation';

import type { HorizontalBarGraphWithAveragesOpinionOptions }
  from '../types/horizontal-bar-graph-with-averages-opinion-options';

interface InternalOptions {
  options: HorizontalBarGraphWithAveragesOpinionOptions;
  datasets: ChartDataset<'bar', number[]>[];
}

/**
 * The HorizontalBarGraphWithAveragesOpinionInternal class contains helper methods for the
 * HorizontalBarGraphWithAveragesOpinion class.
 */
export default class HorizontalBarGraphWithAveragesOpinionInternal {
  /**
   * Get the chart data
   *
   * @param {Object} param - The parameter object.
   * @param {HorizontalBarGraphWithAveragesOpinionOptions} param.options - Additional options for
   *                                                                       the bar graph.
   * @param {ChartDataset<'bar', number[]>[]} param.datasets - The datasets for the bar graph.
   * @returns The chart data, and the goal dataset
   */
  public static getChartData({ options, datasets }: Readonly<InternalOptions>): {
    datasets: ChartDataset<'bar', number[]>[];
    goalAnnotations: Record<string, AnnotationOptions>;
  } {
    return {
      datasets: this.getBarData({ options, datasets }),
      goalAnnotations: this.getGoalAnnotations({ options, datasets }),
    };
  }

  /**
   * Get the dataset for the bar graph
   *
   * @param {Object} param - The parameter object.
   * @param {HorizontalBarGraphWithAveragesOpinionOptions} param.options - Additional options for
   *                                                                       the bar graph.
   * @param {ChartDataset<'bar', number[]>[]} param.datasets - The datasets for the bar graph.
   */
  public static getBarData({ options, datasets }: Readonly<InternalOptions>):
  ChartDataset<'bar', number[]>[] {
    return datasets
      .filter((dataset) => dataset.label !== 'Avg. of all attempts')
      .map((dataset) => ({
        ...dataset,
        backgroundColor: [...options.barColors ?? []],
      }));
  }

  /**
   * Get the data for the goal line
   *
   * @param {Object} param - The parameter object.
   * @param {HorizontalBarGraphWithAveragesOpinionOptions} param.options - Additional options for
   *                                                                       the bar graph.
   * @param {ChartDataset<'bar', number[]>[]} param.datasets - The datasets for the bar graph.
   * @return The dataset for the goal line
   */
  public static getGoalAnnotations({ options, datasets }: Readonly<InternalOptions>):
  Record<string, AnnotationOptions> {
    const annotations: Record<string, AnnotationOptions> = {};

    datasets
      .filter((dataset) => dataset.label === 'Avg. of all attempts')
      .forEach((dataset) => {
        dataset.data.forEach((value, i) => {
          annotations[`goalLine-${i}`] = {
            type: 'line',
            xMin: value,
            xMax: value,
            yMin: i - 0.4,
            yMax: i + 0.4,
            borderColor: options.goalColor,
            borderWidth: 5,
          };
        });
      });

    return annotations;
  }
}
