export default class ColorHelper {
  /**
   * Get a new shade based on a hex color and a magnitude
   *
   * @param hexColor - The color you want to change
   * @param magnitude - How much you want the color to change
   * @returns The new hexadecimal color
   */
  public static newShade({ hexColor, magnitude }:
  Readonly<{ hexColor: string; magnitude: number }>): string {
    const strippedColor = hexColor.replace('#', '');

    if (strippedColor.length !== 6) {
      return hexColor;
    }

    const decimalColor = parseInt(strippedColor, 16);

    // Extract red component
    let r = Math.floor(decimalColor / (256 * 256)) + magnitude;
    if (r > 255) r = 255;
    if (r < 0) r = 0;

    // Extract green component
    let g = Math.floor((decimalColor % (256 * 256)) / 256) + magnitude;
    if (g > 255) g = 255;
    if (g < 0) g = 0;

    // Extract blue component
    let b = (decimalColor % 256) + magnitude;
    if (b > 255) b = 255;
    if (b < 0) b = 0;

    // Combine the components back into a single color value
    const combinedColor = (r * 256 * 256) + (g * 256) + b;
    return `#${combinedColor.toString(16).padStart(6, '0')}`;
  }
}
