export default class RangeLabelsHelper {
  /**
   * Generates range labels for a dashboard graph based on the provided configuration.
   *
   * This function creates a two-dimensional array of string labels that represent
   * either predefined block ranges or default ranges divided into equal parts. Optionally,
   * these labels can be replaced with custom block names if provided.
   *
   * @param {Object} params - The parameters for generating range labels.
   * @param {boolean} [params.followBlock] - Determines whether to use predefined block ranges.
   * @param {string[]} [params.blockRanges] - An array of block range strings (e.g., "0,10").
   *                                           Required if `followBlock` is true.
   * @param {number} params.nrOfBars - The number of bars in the graph. Used to calculate
   *                                   default ranges if `followBlock` is false.
   * @param {(string | undefined)[]} [params.blockNames] - An optional array of block names
   *                                                       to replace the generated range labels.
   *                                                       Undefined entries are ignored.
   * @returns {string[][]} A two-dimensional array of range labels. Each inner array contains
   *                       a single string representing a range or block name.
   *
   * @throws {Error} If `followBlock` is true but `blockRanges` is not defined.
   *
   * @example
   * // Example 1: Using predefined block ranges
   * const labels = RangeLabelsHelper.getRangeLabels({
   *   followBlock: true,
   *   blockRanges: ["0,10", "10,20", "20,30"],
   *   nrOfBars: 3,
   *   blockNames: [undefined, "Custom Block", undefined],
   * });
   * // Output: [["0-10"], ["Custom Block"], ["20-30"]]
   *
   * @example
   * // Example 2: Using default ranges
   * const labels = RangeLabelsHelper.getRangeLabels({
   *   followBlock: false,
   *   blockRanges: [],
   *   nrOfBars: 4,
   *   blockNames: [undefined, "Block A", undefined, "Block B"],
   * });
   * // Output: [["0-25"], ["Block A"], ["50-75"], ["Block B"]]
   */
  public static getRangeLabels({
    followBlock, blockRanges, nrOfBars, blockNames,
  }:
  Readonly<{
    followBlock?: boolean;
    blockRanges?: string[];
    nrOfBars: number;
    blockNames?: (string | undefined)[];
  }>): string[][] {
    const labels: string[][] = [];

    if (followBlock) {
      if (blockRanges === undefined) {
        throw new Error(
          'blockRanges is not defined, but should be defined if followBlock is enabled',
        );
      }

      // Ranges based on the defined blocks
      blockRanges.forEach((blockRange) => labels.push([blockRange.replace(',', '-')]));
    } else {
      // Default ranges from 0 - 100 divided into equal parts
      const step = 100 / nrOfBars;
      for (let i = 0; i < 100; i += step) {
        labels.push([`${Math.ceil(i)}-${Math.ceil(i + step)}`]);
      }
    }

    // Replace the range labels with blockNames if they are defined
    if (blockNames !== undefined) {
      blockNames.forEach((blockName, i) => {
        if (blockName !== undefined) {
          labels[i] = [blockName];
        }
      });
    }

    return labels;
  }
}
